/* src/App.module.css */


.sectionsContainer {
  display: flex;
  height: 100vh;
  position: relative;
}

.section {
  flex: 1;
  height: 94vh;
  background-size: cover;
  background-position: center;
  position: relative;
  transition: flex 1.3s, background-size 1.3s, background-position 1.3s;
  cursor: pointer;
}

.leftSection {
  background-image: url('./assets/backgroundsuit.png');
}

.rightSection {
  background-image: url('./assets/backgrounddeveloper.png');
}

.hovered {
  flex: 3;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.topic  {
  font-size: 2rem;
  margin-bottom: 20px;
  margin-top: 10vh;
  z-index: 2;
  color: rgb(149, 149, 149);
  white-space: nowrap;
  transition: font-size 1s ease;
}

.topicfullstack {
  font-size: 2rem;
  margin-bottom: 20px;
  margin-top: 10vh;
  z-index: 2;
  color: rgb(149, 149, 149);
  white-space: nowrap;
  transition: font-size 1s ease;
}



.hovered .topic {
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.hovered .topicfullstack {
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.middleImageContainer {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: 70%;
  display: flex;
  align-items: center;
  z-index: 3;
}

.personImage {
  height: 70%;
  margin-top: 17vh;
  transition: clip-path 1.3s, transform 1.3s, opacity 0.5s;
  opacity: 0.8;
}

.leftImage {
  clip-path: inset(0 50% 0 0);
  transform: translateX(50%);
}

.rightImage {
  clip-path: inset(0 0 0 50%);
  transform: translateX(-50%);
}

.fullLeft {
  clip-path: inset(0 0 0 0);
  transform: translateX(70%);
}

.fullRight {
  clip-path: inset(0 0 0 0);
  transform: translateX(-70%);
}

.hidden {
  opacity: 0;
  transform: scale(0.95);
}

.projectCardsContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 50px 0;
  margin-bottom: 15vh;
}

.typewritingText {
  position: absolute;
  text-align: left;
  top: 20vh;
  left: 70vh;
  color: #00ff00;
  font-family: 'Courier New', Courier, monospace;
  white-space: pre-wrap;
  font-size: 0.8rem;
}

.businessContentContainer {
  display: flex;
  flex-wrap: wrap;
  width: 85vh;
  margin-top: -5vh;
  justify-content: center;
  align-items: flex-start;
  height: 120%;
  padding: 20px;
}

.businessCard {
  background: none;
  color: white;
  padding: 20px;
  font-size: 12px;
  border-radius: 10px;
  text-align: left;
  height: 250px;
  margin: 10px;
  width: calc(33.33% - 20px);
  box-sizing: border-box;
  opacity: 0.1;
  margin-bottom: 2vh;
  animation: fadeIn 1s ease-in-out forwards;
}

.businessCard:nth-child(4),
.businessCard:nth-child(5) {
  width: calc(50% - 20px);
  margin-top: -24vh;
}

.businessCard h3 {
  margin: 0;
}

.businessCard ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: left;
}

.businessCard ul li {
  margin: 5px 0;
}

@keyframes fadeIn {
  to {
    opacity: 0.9;
  }
}

.businessCard:nth-child(1) {
  animation-delay: 0.5s;
}

.businessCard:nth-child(2) {
  animation-delay: 1s;
}

.businessCard:nth-child(3) {
  animation-delay: 1.5s;
}

.businessCard:nth-child(4) {
  animation-delay: 2s;
}

.businessCard:nth-child(5) {
  animation-delay: 2.5s;
}

.seeMoreButton {
  background-color: transparent;
  opacity: 1;
  color: rgb(255, 255, 255);
  width: 200px;
  height: 50px;
  font-size: 20px;
  z-index: 10;
  font-weight: bold;
  border-radius: 20px;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: -30vh;
}

.seeMoreButton:hover {
  text-decoration: underline;
}

.MyExpertise {
  color: rgb(0, 0, 0);
  font-size: 40px;
  margin-left: 90.5vh;
}

.experienceCardsContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}

.SoloProjects {
  font-size: 3em;
  margin-left:85.5vh;
}

.GroupProjects {
  font-size: 3em;
  margin-left: 85vh;
}

.custom-cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: blue;
  border-radius: 50%;
  pointer-events: none;
  transition: transform 0.1s ease-out;
  transform: translate(-50%, -50%);
  z-index: 10000;
}

.seeBusinessButton {
  background-color: #d72424;
  opacity: 0.7;
  color: rgb(255, 255, 255);
  width: 400px;
  height: 50px;
  font-size: 20px;
  font-weight: bold;
  border: none;
  border-radius: 20px;
  margin-bottom: 10vh;
  margin-top: 5vh;
  margin-left:82vh;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.seeBusinessButton:hover {
  opacity: 1;
}

/* Responsive Design */
@media (max-width: 768px) {

  html, body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }

  .sectionsContainer {
    flex-direction: row;
    height: 50vh;
    width: 20vw;
  }

  

  .topic {
    display: flex;
    justify-content: left;
    width: 70vw;
    flex-wrap: wrap;
    font-size: 0.6rem;
    margin-bottom: 20px;
    margin-top: 15vh;
    margin-left: 15px;
    z-index: 2;
    color: rgb(255, 255, 255);
    white-space: normal; /* Allow text to wrap */

  }

 .topicfullstack
 {
  display: flex;
  justify-content: left;
  width: 70vw;
  flex-wrap: wrap;
  font-size: 0.6rem;
  margin-bottom: 20px;
  margin-top: 15vh;
  margin-left: 15px;
  z-index: 2;
  color: rgb(0, 234, 47);
  white-space: normal; /* Allow text to wrap */

}

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  .hovered {
    flex: none;
  }

  .sectionsContainer {
    display: flex;
    height: 50vh;
    position: relative;
  }

  .section {
    height: auto;
    width: 50vw;
    transition: none;
    cursor: default;
  }

  

  .middleImageContainer {
    top: auto;
    width:100vw;
    left: 25vh;
    bottom: 85px;
    transform: translateX(-50%);
  }

  .personImage {
    display:flex;
    margin-top: 7.5vh;
    left: 30vh;
    height: auto;
    width: 50%;
  }


  
  .projectCardsContainer,
  .experienceCardsContainer,
  .groupProjectsContainer {
    flex-direction: column;
    align-items: center;
  }

  .projectCard,
  .experienceCard,
  .groupProjectCard {
    width: 90%; /* Adjust the width to make it smaller */
    margin: 20px 0; /* Adjust the margin to add spacing between cards */
  }

  .seeMoreButton,
  .seeBusinessButton {
    width: 80%;
    font-size: 1.2rem;
  }

  .typewritingText {
    top: 10vh;
    left: 5vw;
    font-size: 1rem;
  }

  .businessContentContainer {
    width: 90vw;
    padding: 10px;
    margin-top: 0;
  }

  .businessCard {
    width: 100%;
    margin: 10px 0;
    padding: 10px;
  }


  .seeBusinessButton {
    background-color: #d72424;
    opacity: 0.7;
    color: rgb(255, 255, 255);
    width: 300px;
    height: 50px;
    font-size: 14px;
    font-weight: bold;
    border: none;
    border-radius: 20px;
    margin-bottom: 10vh;
    margin-top: 5vh;
    margin-left: 6vh;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s;
  }


  .MyExpertise {
    color: rgb(0, 0, 0);
    font-size: 40px;
    margin-left: 12vh;
  }
  
  .SoloProjects {
    font-size: 3em;
    margin-left:6.5vh;
  }
  
  .GroupProjects {
    font-size: 3em;
    margin-left: 5vh;
  }


}
