/* src/components/ExperienceCard.module.css */
.experienceCard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 97%;
    margin-bottom: 80px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: rgb(245, 245, 245);
  }
  
  .leftSection {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start; /* Align items to the left */
  }
  
  .contentWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Align items to the left */
    margin-left: 15vh;
  }
  
  .title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    font-family: 'Sora', sans-serif; /* Applies the Sora font */

  }
  
  .description {
    font-size: 1rem;
    margin-bottom: 10px;
    text-align: left; /* Ensures the text is aligned to the left */
    font-size: 22px;
    margin-bottom: 30px;    
    font-family: 'Plus Jakarta Sans', sans-serif; /* Applies the Plus Jakarta font */

   
  }
  
  .techStack {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 10px;
  }
  
  .tech {
    background-color: transparent;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 1.2rem;
  }
  
  .link {
    color: #0073e6;
    text-decoration: none;
    font-weight: bold;
    margin-left: 10px;
    font-size: 17px;
  }
  
  .rightSection {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
  }
  

  .tags {
    margin-bottom: 10px;
  }
  
  .tag {
    background-color: #e99740;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 0.875rem;
    font-weight: bold;
    margin-right: 5px;
  }
  

  @media (max-width: 768px) {
    .experienceCard {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      width: 90%;
      margin-bottom: 20px;
      padding: 20px;
      border: 1px solid #ddd;
      border-radius: 10px;
      background-color: rgb(245, 245, 245);
    }
  
    .leftSection {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
    }
  
    .contentWrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 15px 0;
      width: 100%;
    }
  
    .title {
      font-size: 1.5rem;
      font-weight: bold;
      margin-bottom: 10px;
      font-family: 'Sora', sans-serif; /* Applies the Sora font */
      text-align: center;
    }
  
    .description {
      font-size: 0.9rem;
      margin-bottom: 10px;
      text-align: left; /* Ensures the text is centered */
      
      font-family: 'Plus Jakarta Sans', sans-serif; /* Applies the Plus Jakarta font */
    }
  
    .techStack {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      margin-bottom: 10px;
      justify-content: center; /* Center tech stack items */
      width: 100%; /* Ensure the container doesn't exceed the screen width */
    }
  
    .tech {
      background-color: transparent;
      padding: 5px 10px;
      border-radius: 5px;
      font-size: 1.2rem;
    }
  
    .link {
      color: #0073e6;
      text-decoration: none;
      font-weight: bold;
      margin-left: 10px;
      font-size: 1rem;
    }
  
    .rightSection {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      margin-top: 10px;
    }
  
    .image {
      max-width: 100%;
      height: auto;
      border-radius: 10px;
    }
  
    .tags {
      display: flex;
      flex-wrap: wrap; /* Allow the tags to wrap within the container */
      justify-content: center; /* Center the tags */
      width: 100%; /* Ensure the container doesn't exceed the screen width */
      margin-bottom: 10px;
    }
  
    .tag {
      background-color: #e99740;
      color: white;
      padding: 5px 10px;
      border-radius: 5px;
      font-size: 0.875rem;
      font-weight: bold;
      margin-right: 5px;
      margin-bottom: 5px; /* Add margin to the bottom for better spacing when wrapping */
    }
  }
  
  