/* src/components/TopBar.module.css */
.topBar {
    width: 100%;
    display: flex;
    justify-content: right; /* Center items */
    align-items: right;
    background-color: none;
    padding: 20px 0;
    position: fixed;
    top: 0;
    left: -40px;
    z-index: 1000;
  }
  
  .navButton {
    background-color: transparent;
    color: white;
    font-size: 20px;
    border: none;
    padding: 10px 15px; /* Reduce padding for closer buttons */
    margin: 0 5px; /* Add margin to control spacing between buttons */
    cursor: pointer;
    transition: background-color 0.1s;
    margin-left: 50px;
  }
  
  .navButton:hover {
    background-color: #555;
    border-radius: 20px;

  }
  
  .navButton.active {
    background-color: #0c09c1; /* Highlighted color for active button */
    border-radius: 20px;

  }


  @media (max-width: 768px)  {


    .topBar {
      width: 100%;
      margin-left: 8vw;
      display: flex;
      justify-content: right; /* Center items */
      align-items: right;
      background-color: none;
      padding: 20px 0;
      position: fixed;
      top: 0;
      left: -40px;
      z-index: 1000;
    }
    
  .navButton {
    background-color: transparent;
    color: white;
    font-size: 12px;
    border: none;
    padding: 2px 4px; /* Reduce padding for closer buttons */
    cursor: pointer;
    transition: background-color 0.1s;
    margin-left: 20px;
  }
  
  .navButton:hover {
    background-color: #555;
    border-radius: 20px;

  }
  
  .navButton.active {
    background-color: #0c09c1; /* Highlighted color for active button */
    border-radius: 20px;

  }

  }