/* src/components/GroupProject.module.css */
.groupProjectsContainer {
    display: flex;
    overflow-x: auto;
    width: 100%;
    box-sizing: border-box;
  }
  
  .groupProjectCard {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 10px 12px 19px rgba(0, 0, 0, 0.1);
    flex: 0 0 auto;
    padding: 20px;
    width: 25vw;
    margin-left: 3vw;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5vh;

  }
  
  .image {
    width: 50vh;
    height: 20vh;
    border-radius: 10px;
    margin-bottom: 15px;
  }
  
  .title {
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: left;
  }
  
  .description {
    font-size: 1rem;
    text-align: center;
    text-align: left;
  }
  
  .liveSiteLink {
    font-size: 1rem;
    color: #0073e6;
    text-decoration: none;
    font-weight: bold;
    margin-top: 10px;
  }
  


  @media (max-width: 768px) { 

    .groupProjectsContainer {
      display: flex;
      overflow-x: auto;
      width: 100vw;
      box-sizing: border-box;
    }
    
    .groupProjectCard {
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1);
      flex: 0 0 auto;
      padding: 20px;
      width: 65vw;
      margin-left: 3vw;
      
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 5vh;
  
    }
    
    .image {
      width: 30vh;
      height: 20vh;
      border-radius: 10px;
      margin-bottom: 15px;
    }
    
    .title {
      font-size: 1.4rem;
      font-weight: bold;
      margin-bottom: 10px;
      text-align: left;
    }
    
    .description {
      font-size: 1rem;
      text-align: center;
      text-align: left;
    }
    
    .liveSiteLink {
      font-size: 1rem;
      color: #0073e6;
      text-decoration: none;
      font-weight: bold;
      margin-top: 10px;
    }

    

    

  }